<template>
  <div class="home-container">
    <van-nav-bar title="黑马头条" fixed />
    <!-- 禁用下拉刷新到值要等于finished的值 -->
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh" :disabled="finished">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <ArticleInfo
          v-for="item in artlist"
          :key="item.id"
          :title="item.title"
          :author="item.aut_name"
          :cmt-count="item.comm_count"
          :time="item.pubdate"
          :cover="item.cover"
        ></ArticleInfo>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
// 导入封装好的axios
import { getArticleListAPI } from '@/api/aricleAPI.js'
import ArticleInfo from '@/components/Article/ArticleInfo.vue'
import _ from 'lodash'
let fn = null
export default {
  created() {
    this.initArticleList()
  },
  activated() {
    fn = this.recordTopHandler()
    window.addEventListener('scroll', fn)
  },
  deactivated() {
    window.removeEventListener('scroll', fn)
  },
  name: 'HomeCom',
  data() {
    return {
      page: 1,
      limit: 10,
      artlist: [],
      // 是否正在加载下一页
      loading: true,
      // 是否加载完毕,加载完毕一定要改成true
      finished: false,
      refreshing: false
    }
  },
  methods: {
    async initArticleList(isRefresh) {
      const { data: res } = await getArticleListAPI(this.page, this.limit)
      if (isRefresh) {
        // 下拉刷新
        // 新前旧后
        this.artlist = [...res, ...this.artlist]
        this.refreshing = false
      } else {
        // 上拉加载
        // 旧前新后
        this.artlist = [...this.artlist, ...res]
        this.loading = false
      }
      if (res.length === 0) {
        this.finished = true
      }
    },
    // 下拉到底部会触发load事件，，因此用该函数监听，发起ajax请求
    // 一进来页面也会触发load事件,但是一进来的数据在create已经创建了，所以一进来不应该发起请求
    // 把loading默认设置为true，就无法触发load事件，只有为false时，才可能触发load事件
    // 然后加载完第一页数据，又变成false，可以下次触发
    onLoad() {
      this.page++
      this.initArticleList()
    },
    onRefresh() {
      // 让页面+1，把第二页显示到第一页上面
      this.page++
      this.initArticleList(true)
    },
    recordTopHandler() {
      // _.debounce返回一个防抖函数,当事件触发时，延迟毫秒数再执行，如果在wait时间内再次出发就重新计算wait时间
      // option参数中leading表示，先执行再延迟wait秒，trailing表示延迟wait秒再执行，看情况而定
      // 默认就是trailing为true
      return _.debounce(
        () => {
          this.$route.meta.top = window.scrollY
        },
        500,
        {
          trailing: true
        }
      )
    }
  },
  components: {
    ArticleInfo
  }
}
</script>

