import Vue from 'vue'
import App from './App.vue'
import router from './router'
/* // 全部导入并安装vant
import Vant from 'vant'
import 'vant/lib/index.less'
Vue.use(Vant) */
// 按需导入
// 1，安装自动按需导入的插件
// 2.在babel中配置
// 3.就可以按import使用

// --------------Vant开始---------------
import {
  Button,
  Tabbar,
  TabbarItem,
  Icon,
  Cell,
  CellGroup,
  Tag,
  NavBar,
  List,
  PullRefresh
} from 'vant'

Vue.use(Tabbar)
Vue.use(TabbarItem)
Vue.use(Icon)
Vue.use(Button)
Vue.use(Cell)
Vue.use(CellGroup)
Vue.use(Tag)
Vue.use(NavBar)
Vue.use(List)
Vue.use(PullRefresh)
// --------------Vant结束---------------

Vue.config.productionTip = false

new Vue({
  router,
  render: (h) => h(App)
}).$mount('#app')
