import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home/Home.vue'
import User from '@/views/User/User.vue'
Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/home' },
  // 给这个路由对象附加一些信息
  { path: '/home', component: Home, meta: { isRecord: true, top: 0 } },
  { path: '/user', component: User }
]

const router = new VueRouter({
  routes,
  // 这个名字时固定的，切换路由使组件开启就会调用该函数，这个函数返回什么xy，该组件就会滚动到哪个xy，前提是组件不能被销毁
  scrollBehavior(to, from, savedPosition) {
    // 1.滚动行为---浏览器前进后退切换路由
    // return 期望滚动到哪个的位置x,y
    // 当浏览器按下前进后退按钮后，也会切换路由，此时也会自动调用scrollBehavior()
    // 调用函数，自动传进来savedPosition，是一个对象{x:12,y:12}，表示屏幕组件左上角离该组件原来左上角的位置
    // 当用组件里的link切换路由，也会调用该函数，但此时savedPosition为null，只有浏览器前进后退它才有值
    // 浏览器前进后退切换路由时，不管组件有没有alive，savedPosition都有值，说明组件被切换销毁前，就保存了滚动到哪个位置都值
    // 虽然浏览器前进后退都会得到当前组件的原来的滚动位置，但是原来组件已经被销毁，此时返回savedPosition没有意义
    // 如果切换时，那个组件没被销毁，而且暂存起来，那么return savedPosition就有意义，因此要让保存原来滚动位置的组件alive
    console.log(from, to, savedPosition)
    if (savedPosition) {
      return savedPosition
      // 2.滚动行为----点击link切换路由
      // link切换路由savedPosition都是null，所以如果返回savedPosition都是没有用，无法记录上次滚动到哪里
      // 那就需要手动去记录上次滚动到哪里，好在每个组件都有自己的一个路由对象,路由对象的meta属性可以存信息
      // 可以在滚动组件上将滚动到了位置存起来，然后在用to获取meta信息，滚动到上一次的位置
    } else {
      const lastPosition = { x: 0, y: to.meta.top || 0 }
      return lastPosition
    }
  }
})
// 路由守卫
// router.beforeEach((to, from, next) => {
// to和from都是路由对象,也称为路由记录，里面有params，path，fullpath，meta属性
// 每个组件的this.$route就是路由对象
// console.log(to)
// next()
// })
export default router
